import { loadData } from '@/api/common'
import FootNav from '@/components/foot-nav'
import Scroll from '@/components/scroll'
import Tips from './components/tips'
import { mapMutations } from 'vuex'
export default {
  components: {
    FootNav,
    Scroll,
    Tips
  },
  data() {
    return {
      isShow: false,
      signIn: true,
      phone: '',
      sign: 0,
      enterpriseArticle: {}
    }
  },
  created() {
    this.initDate()
  },
  methods: {
    ...mapMutations(['changeLogin']),
    initDate() {
      if (localStorage.getItem('AccessToken')) {
        this.isShow = true
        loadData('/member/center').then(data => {
          console.log(data)
          this.phone = data.data.phone
          console.log(data.data.isSign)
          if (data.data.isSign === true) {
            this.signIn = false
          } else {
            this.signIn = true
          }
          this.sign = data.data.sign
        })
      } else {
        this.isShow = false
      }
    },
    toSignIn() {
      loadData('/member/sign/save').then(data => {
        this.$refs.Tips.show()
        this.sign += 1
        this.signIn = false
      })
    }
  }
}



